import React, {useContext, useEffect, useState} from 'react';
import {action, makeAutoObservable} from 'mobx';
import {observer} from 'mobx-react-lite';
import axios from 'axios';
import {apiBase} from './utilities';

export const StoreContext = React.createContext();
export const useStore = () => useContext(StoreContext);

export const NetworkManagerMode = {
    Offline: 0,
    ServerOnly: 1,
    ClientOnly: 2,
    Host: 3,
};

export const Blocks = {
    None: 0,
    Wood: 1,
    Orange: 2,
    Green: 3,
    Brown: 4,
    Pink: 5,
    LightPurple: 6,
    DarkPurple: 7,
    Violet: 8,
    LightNeutral: 9,
    DarkNeutral: 10,
};

export function showBlock(block){
    switch (block){
        case Blocks.None:
            return "None"
        case Blocks.Wood:
            return "Wood"
        case Blocks.Orange:
            return "Orange"
        case Blocks.Green:
            return "Green"
        case Blocks.Brown:
            return "Brown"
        case Blocks.Pink:
            return "Pink"
        case Blocks.LightPurple:
            return "Light Purple"
        case Blocks.DarkPurple:
            return "Dark Purple"
        case Blocks.Violet:
            return "Violet"
        case Blocks.LightNeutral:
            return "Light Neutral"
        case Blocks.DarkNeutral:
            return "Dark Neutral"
        default:
            return `Unknown (${block})`
    }
}

class Store {
    SocialInfo = {
        UserName: 'NoName',
    };
    AppInfo = {
        Build: 'DEVELOPMENT',
        MicrophonePermission: true,
        Version: '?',
        BuildId: -1,
    };
    _networkInfo = {
        Online: true,
        NetworkAddress: 'none',
        RoomOpen: false,
        Mode: NetworkManagerMode.Offline,
    };
    ContextInfo = {
        LeftBlockActive: Blocks.None,
        RightBlockActive: Blocks.None,
        LeftBlockBreak: false,
        RightBlockBreak: false,
    }
    MediaInfo = {
        Active: false,
        Name: 'None',
        Paused: true,
        Scrub: 0,
        Duration: 1,
        VolumeLevel: 0,
    };
    ExperimentalInfo = {
        BlockBreakEnabled: false,
        PinchPullEnabled: false,
    };
    PlayerInfos = [];
    LoadingRoomCode = false;
    _refresh_room_code_handler = null;
    RoomSecret = '';
    _roomCode = null;

    constructor() {
        makeAutoObservable(this);
    }

    get FullVersion() {
        return this.AppInfo.Version + 'b' + this.AppInfo.BuildId;
    }

    get NetworkInfo() {
        return this._networkInfo;
    }

    set NetworkInfo(networkInfo) {
        this._networkInfo = networkInfo;
        if (!networkInfo.RoomOpen) {
            this.RoomCode = '';
        }

    }

    get RoomCode() {
        return this._roomCode;
    }

    set RoomCode(code) {
        this._roomCode = code;
        if (code) {
            this._refresh_room_code_handler = setInterval(() => {
                        if (this.RoomCode) {
                            this.refreshRoomCode();
                        }
                    }
                    , 1000);
        } else {
            clearInterval(this._refresh_room_code_handler);
            this._refresh_room_code_handler = null;
            this.RoomSecret = '';
        }
    }

    _room_open = false;

    get room_open() {
        return this._room_open;
    }

    set room_open(open) {
        this._room_open = open;
        if (!open) {
            this.RoomCode = '';
        }
    }

    refreshRoomCode() {
        let url = apiBase(this) + `/rooms/${store.RoomCode}/refresh`;
        console.log(url);
        axios({
            method: 'post',
            url: url,
            data: `secret=${this.RoomSecret}`,
            header: {'content-type': 'application/x-www-form-urlencoded'},
        }).catch(err => {
            console.log(err);
            this.RoomCode = null;
        });
    }
}

const store = new Store();
Object.seal(store);
let pushStoreList = action((kvList) => {
    kvList.forEach(kv => {
        store[kv.Key] = kv.Val;
    });
});
let pushStore = action(obj => {
    for (const prop in obj) {
        store[prop] = obj[prop];
    }
});
let pushStoreSingle = action(obj => {
    store[obj.Key] = obj.Val;
});

function useListeners() {
    let [init, setInit] = useState(false);

    useEffect(() => {
        if (init) return;

        setInit(true);

        let storeListeners = event => {
            let json = JSON.parse(event.data);
            switch (json.Type) {
                case 'command':
                    switch (json.Message) {
                        case 'pushStore':
                            pushStoreList(json.Data);
                            break;
                        case 'pushStoreSingle':
                            pushStoreSingle(json.Data);
                            break;
                        default:
                            console.log('message not handled ' + event.data);
                            break;
                    }
                    break;
                default:
                    break;
            }

        };

        if (window.vuplex != null) {
            console.log('bonsai: vuplex is not null -> storeListeners');
            window.vuplex.addEventListener('message', storeListeners);
        } else {
            console.log('bonsai: vuplex is null');
            window.addEventListener('vuplexready', _ => {
                console.log('bonsai: vuplexready -> storeListeners');
                window.vuplex.addEventListener('message', storeListeners);
            });
        }

    }, [init]);
}

export const StoreProvider = observer(({children}) => {

    useListeners();

    return <StoreContext.Provider
            value={{
                store,
                pushStore,
                pushStoreList,
            }}>{children}</StoreContext.Provider>;
});